.cart__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 463px) {
        flex-wrap: wrap;
        gap: 15px;
        justify-content: center;
    }
}

.cart__middle {
    @media (max-width: 544.98px) {
        display: flex;
        align-items: center;
        gap: 35px;
        overflow: auto;
    }

}
.cart__bottom{
    margin-top: 15px;
}
.cart .content__title svg {
    position: relative;
    top: -2px;
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.cart__clear span,
.cart__clear svg,
.cart__clear path {
    transition: all .15s ease-in-out;
}

.cart .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;
    @media (max-width: 544.98px) {
        font-size: 22px;

    }
}

.cart__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;
}

.cart__clear span {
    display: inline-block;
    margin-left: 7px;
    color: #b6b6b6;
    font-size: 18px;
}

.cart__clear:hover span,
.cart__clear:hover svg path {
    stroke: #373737;
    color: #373737;
}

.cart__bottom-details {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    margin: 40px 0 20px;

    @media (max-width: 463px) {
        flex-direction: column;
    }

}

.cart__bottom-details span {
    font-size: 22px;
    @media (max-width: 544.98px) {
        font-size: 16px;
        
    }

}

.cart__bottom-details span:last-of-type b {
    color: #fe5f1e;
}

.cart__bottom-buttons {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: center;
    margin-top: 15px;

    @media (max-width: 544.98px) {
        flex-direction: column-reverse;
        align-items: center;
        gap: 20px;
    }
}

.cart__bottom-buttons .go-back-btn {
    display: flex;
    height: 50px;
    align-items: center;
    justify-content: center;
    width: 210px;
    gap: 10px;
    color: white;
    border-color: #000;
    border-radius: 30px;
    background-color: black;
}

.cart__bottom-buttons .go-back-btn svg {
    margin-right: 12px;
}

.cart__bottom-buttons .go-back-btn span {
    color: #ffffff;
    font-weight: 600;
}

.cart__bottom-buttons .pay-btn {
    font-size: 16px;
    font-weight: 600;
    width: 210px;
    padding: 16px;
    color: white;
    display: inline-block;
    background-color: #fe5f1e;
    border-radius: 30px;
    text-align: center;
    cursor: pointer;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out;
    border: 1px solid transparent;
    min-width: 210px;
    min-height: 53px;
    user-select: none;

    &:active {
        padding: calc(0.25em + 1px) 0.5em calc(0.25em - 1px);
    }
}

.cart__bottom-buttons .go-back-btn:hover {
    background-color: #111;
    border-color: #111;
}

.button--outline:hover,
.button--outline:hover span {
    color: #fff;
}

.button--outline:active {
    background-color: #f24701;
}

.button:hover {
    background-color: #f24701;
}