.container--box {
    max-width: 820px;
}

.box__item {
    display: flex;
    margin-top: 30px;
    align-items: center;
    gap: 30px;
    justify-content: space-around;

}

.box__item-img img {
    width: 100%;
}

.box__item-img {
    display: flex;
    align-items: center;
    max-width: 40px;
}

.box__item-info h3 {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.01em;
}

.box__item-remove .button {
    border-color: #ddd;
}

.button--outline,
.button--outline span {
    color: #fe5f1e;
}

.button--circle {
    border-radius: 30px;
    text-align: center;
    cursor: pointer;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out;
    border: 1px solid transparent;
    user-select: none;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    min-width: 32px;
    border: 1px #fe5f1e solid;
    padding: 0;
    border-width: 2px;

    @media (max-width: 563px) {
        flex-direction: column;
        width: 40px;
        height: 40px;
    }
}

.button--circle:active {
    padding: calc(0.25em + 1px) 0.5em calc(0.25em - 1px);

}

.box__item-remove .button:hover,
.box__item-remove .button:active {
    border-color: #2a2a2a;
    background-color: #2a2a2a;
}

.box__item-remove svg {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.box__item-remove .button svg {
    width: 11.5px;
    height: 11.5px;
    position: relative;
}

.button--outline:hover svg path {
    fill: #fff;
}

.button--outline svg path {
    fill: rgb(0, 0, 0);
}