.header {
  background: #ffffff;
  padding: 0px 0px 3px 0px;
  margin-bottom: 8px;
  position: fixed;
  width: 100%;
  bottom: -2%;
  z-index: 2;
  left: 50%;
  transform: translate(-50%);
}
.container_short{
  padding: 0px 10px;
}

hr {
  border: #F6F6F6 solid 1px;
}

.header__row {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  padding: 4px 0px 10px 0px;
  align-items: center;
  gap: 17px;
}

.header__btn{
  width: 40px;
  height: 40px;
}
.header__btn_center{
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.header__item {
  display: flex;
  gap: 10px;
  align-items: center;

}

.header__box {
  width: 30px;
  height: 30px;
}

.button--cart {
  display: flex;
  align-items: center;
  line-height: 23px;
  background-color: #fe5f1e;
  border-radius: 30px;
  padding: 10px 9px;
min-width: 132px; 
justify-content: center;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
  border: 1px solid transparent;
  user-select: none;
  max-width: 175px;
}

.header .search__input {
  display: none;
}

.header .search {
  border: 0;
  padding: 0;
}

.logo {
  vertical-align: super;
}

.button--cart:hover {
  background-color: #f24701
}

.button--cart span {
  font-weight: 600; 
  font-size: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.button__delimiter {
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.25);
  margin-left: 10px;
  margin-right: 10px;
}

.button--cart svg {
  margin-right: 8px;
margin-bottom: 1px;
min-width: 18px;
}