.root{
  padding: 10px 20px 10px;
  background-color: #FFAB08;
  min-height: 100%;
  overflow: hidden;
}
.page{
  background-color: white;
  padding: 15px 0px 10px;
  border-radius: 10px;
  max-width: 1500px;
  margin: auto;
  animation: page;
  opacity: 0;
  animation-duration: 0.8s;
  animation-delay: 1.5s;
  animation-fill-mode: both;
}
@keyframes page{
  from{
    display: none;
  }
   to{ 
    display: block;
      opacity: 1;
   }
}