.modal {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 4;
	width: 100%;
	height: 100vh;
	background: #00000024;
	display: flex;

	justify-content: center;
	align-items: center;
	
	// .modal_msg

	&_msg {
		color: white;
		font-weight: bold;
		font-size: 22px;
		text-align: center;
		letter-spacing: 0.5px;
	}

	// .modal_btn

	&_btn {
		position: absolute;
		left: 50%;
		padding: 14px 26px;
		bottom: 19%;
		font-weight: 600;
		border: 0;
		border-radius: 45px;
		font-weight: bold;
		font-size: 16px;
		line-height: 6px;
		
		text-align: center;
		letter-spacing: 0.5px;
		color: #f24701;
		background: #fff;
		transform: translate(-50%);

	}
	
	&__close_white{
		color: white;
		position: absolute;
		top: 8%;
		left: 90%;
		font-size: 24px;
		font-weight: bold;
	}
	// .modal__body

	&__body {
		position: relative;
		padding: 56px 30px;
		background: #eb5a1e;
		border-radius: 10px;
		max-width: 649px;
		width: 80%;
		padding-bottom: 87px;

	}
}