.sort {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}
.sort__label {
    display: flex;
    align-items: center;
}
.svg__trans{
    transform: rotate(180deg);
}
.sort__label svg{
    transition: all 0.5s;
}
.sort__popup {
    position: absolute;
    transition: all 1s;
    background: #fff;
    box-shadow: 0px 5px 15px #00000017;
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 100%;
    z-index: 1;
    top: 30px;
}
.sort__label *:not(:last-child) {
    margin-right: 8px;
}
.sort__label span {
    color: #fe5f1e;
    border-bottom: 1px dashed #fe5f1e;
    cursor: pointer;
}
.sort__popup ul {
    overflow: hidden;
}
.sort__popup ul li.active {
    font-weight: bold;
    color: #fe5f1e;
    background: rgba(254,95,30,0.05);
}
.sort__popup ul li:hover{
    background: rgba(254,95,30,0.05);
} 
.sort__popup ul li {
    padding: 12px 20px;
    cursor: pointer;
    font-weight: 700;
    background: #ffffff;
    border-radius: 0px;
    transition: background-color 0.1s ease-in-out;
}